import React,{Component} from 'react'
import theme from "../theme.json";
class ToggleTheme extends Component {
  
    state = {
        color1: "white",
        counter:0
      };
      changeColor = () => {
        
        this.setState({
          counter:((++this.state.counter)%theme.length)
          //   color1: this.state.color1 === "white" ? "yellow" : "white"
           });
console.log(this.state.counter);
        console.log('counter: '+this.state.counter+' and theme.length:'+theme.length+' and %:'+this.state.counter%theme.length+
        ' theme['+this.state.counter+']: ')
        document.body.style.setProperty('--first-back-color',theme[this.state.counter-1].first_backcolor);
        document.body.style.setProperty('--first-color',theme[this.state.counter-1].first_color);

        document.body.style.setProperty('--second-back-color',theme[this.state.counter-1].second_backcolor);
        document.body.style.setProperty('--second-color',theme[this.state.counter-1].second_color);

        document.body.style.setProperty('--third-back-color',theme[this.state.counter-1].third_backcolor);
        document.body.style.setProperty('--third-color',theme[this.state.counter-1].third_color);
      };
componentDidMount(){
  // console.log(10%theme.length)
  // console.log(theme[0].first_backcolor)
  // console.log(theme[1].first_backcolor)
  // console.log(theme[2].first_backcolor)
    // document.body.style.setProperty('--first-back-color','#a01c1c');
  }

  render() {
 return(
   <span onClick={() => {
    this.changeColor();
  }}>
       {this.props.children}
   </span>
 )
 }
}
export default ToggleTheme